<template>
    <div id="NEWS">
        <span>1321231231asdasdsa</span>
        <b-table striped hover :items="items" :fields="fields"></b-table>
    </div>
</template>
<script>
    const GetCallInHistoyrUrl = "GetDataApi/GetCallinHistory"
    export default {
        data() {
            return {
                // Note 'isActive' is left out and will not appear in the rendered table
                fields: [
                    {
                        key: 'callinID',
                        sortable: true
                    },
                    
                ],
                items: [
                   
                ]
            }
        },
        methods: {
            getCallInHistory(StartDate, EndDate) {
                fetch(GetCallInHistoyrUrl,
                    {
                        headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({ ServicePhone: "0800668660", StartDate: StartDate, EndDate: EndDate }),
                        method: 'POST',
                    }).then((response) => {
                       
                        return response.json();
                    }).then((jsonData) => {
                        console.log(jsonData)
                    }).catch((err) => {
                        console.log('錯誤:', err);
                    });
            },
        },
        mounted() {
            this.getCallInHistory("2020-01-01","2020-12-31")
        }
    }
</script>
